import React from 'react'
import { Link as ScrollLink, Element } from 'react-scroll'

import cover from './cover.svg'
import bubble from './bubble.svg'
import doctor from './doctor.svg'
import stethoscope from './stethoscope.svg'
import ox from './yes-or-no.svg'
import motion299 from './299.svg'
import waitingNumber from './waitingNumber.svg'

import Container from 'components/Container'
import FullpageVerticalCenter from 'components/FullpageVerticalCenter';
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Link from 'components/Link'
import Image from 'components/Image'
import Button from 'components/Button'
import theme, { mobileOrDesktop } from 'components/ThemeProvider/theme';
import withResponsive from 'hoc/withResponsive'

import Layout from '../Layout';

const Module = ({ question, buttonName, to, src, ...props }) => (
  <Box width={1 / 2} {...props} borderColor="bgOrange">
    <Image src={src} px={mobileOrDesktop('20%', '30%')} />
    <Text whiteSpace="pre-wrap" fontSize={mobileOrDesktop('1.2em', '2em')} mt="1em">{question}</Text>
    <Button mt="0.5em" is={Link} width="11em" to={to}>{buttonName}</Button>
  </Box>
)

const IndexPage = ({ isMobile }) => (
  <Layout bg="bgOrange">
    <FullpageVerticalCenter noFooter>
      <Container mt={mobileOrDesktop('-6em', 0)}>
        <Flex
          alignItems={mobileOrDesktop('initial', 'center')}
          flexWrap={mobileOrDesktop('wrap-reverse', 'nowrap')}
        >
          <Box width={1} textAlign={mobileOrDesktop('center', 'left')} mt="2em">
            <Box position="relative" fontWeight="bold" display="inline-block">
              <Text.h1 fontSize={mobileOrDesktop('2.5em', '3.5em')} color="titleBlue">
                看診好幫手
              </Text.h1>
              <Box position="absolute" left="100%" top="0" transform="translateY(-100%)" width={mobileOrDesktop(0, '4em')}>
                <Image src={bubble} />
              </Box>
            </Box>
            <Text fontSize="1.2em" mt={mobileOrDesktop(0, '0.5em')}>問對問題，看診絕對沒問題</Text>
            <Box>
              <Button.open is={ScrollLink} to="intro" smooth mt={mobileOrDesktop('1em', '3em')}>認識好幫手</Button.open>
            </Box>
          </Box>
          <Box width={1} px={mobileOrDesktop('8%', 0)}>
            <Image src={cover} />
          </Box>
        </Flex>
      </Container>
    </FullpageVerticalCenter>
    <Element name="intro">
      <Box py="4em" bg="lightBgOrange">
        <Container overflowX="hidden">
          <Box textAlign="center" mt={theme.headerHeight}>
            <Box
              textAlign="center"
              color="titleBlue"
              bg="titleBgOrange"
              px={mobileOrDesktop('2em', '8em')}
              py="2em"
              display="inline-block"
              borderRadius="1em"
              position="relative"
              fontWeight="bold"
            >
              <Text.h2 fontSize={mobileOrDesktop('1.75em', '3em')}>看醫生總是不知所措</Text.h2>
              <Text.serif fontSize={mobileOrDesktop('1.2em', '1.75em')}>如何最有效地和醫生溝通？</Text.serif>
              <Box position="absolute" top="0" right="0" transform="translate(50%, -65%)" width={mobileOrDesktop('6em', '8em')}>
                <Image src={motion299} />
              </Box>
              <Box position="absolute" bottom="0" left="0" transform="translate(-50%, 50%)" width={mobileOrDesktop('6em', 'auto')}>
                <Image src={waitingNumber} />
              </Box>
            </Box>
          </Box>
          <Flex
            alignItems="center"
            mt={mobileOrDesktop('3em', '10em')}
            transform={mobileOrDesktop('translateX(-10%)', 'none')}
          >
            <Box width={mobileOrDesktop('47%', 1/3)} px={mobileOrDesktop(0, '2%')}><Image src={doctor} /></Box>
            <Box width={mobileOrDesktop('53%', 2/3)} ml={mobileOrDesktop('1em', '3em')}>
              <Text.h3 fontSize={mobileOrDesktop('1.75em', '3em')}>8個問題</Text.h3>
              {isMobile ? ( <Text.h4 fontSize={mobileOrDesktop('1.2em', '1.75em')}>檢視你看診時<br/>容易忽略的問題</Text.h4>
              ) : (
                <Text.h4 fontSize={mobileOrDesktop('1.2em', '1.75em')}>檢視你看診時容易忽略的問題</Text.h4>
              )}
              <Text mt="1em" lineHeight="1.5">花幾分鐘提早做好準備，就可以讓你在短短幾分鐘的看診時間，<br/>更有效率地和醫生溝通！</Text>
            </Box>
          </Flex>
        </Container>
        <Box mt="3em" textAlign="center" px={mobileOrDesktop('1em', '25%')}>
          <Text.h4 fontSize={mobileOrDesktop('1.2em', '1.5em')}>隨時隨地，為看診做好準備！</Text.h4>
          <Flex mt={mobileOrDesktop('1.5em', '3em')}>
            <Module question="為下次看診做好準備" buttonName="製作備忘錄" src={stethoscope} to="/memo" borderRight="1px solid" />
            <Module question="檢測上次看診的表現" buttonName="看診小測驗" src={ox} to="/test/start" borderLeft="1px solid" />
          </Flex>
        </Box>
      </Box>
    </Element>
  </Layout>
)

export default withResponsive(IndexPage)
